/*COLOR*/
.header {
  background-color: #adb5bd;
  padding: 32px 0 32px 0; }
  .header__logo-name-company {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center; }
  .header__logo {
    height: 20px;
    width: 20px;
    background-color: #fff;
    color: #adb5bd;
    position: relative; }
  .header__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -8px;
    margin-left: -8px; }
  .header__name-company {
    margin: 0;
    padding-left: 20px;
    font-size: 24px;
    color: #fff;
    font-weight: bolder;
    line-height: 20px; }



